import * as React from 'react'
import classNames from 'classnames'
import { Link } from 'gatsby'

export default function Header({ title, className, ...restProps }) {
  const headerClassName = classNames(['', className])

  return (
    <header className={headerClassName} {...restProps}>
      <div className="container mx-auto flex items-center h-[80px]">
        <h1 className="mt-0 text-2xl font-bold">
          <Link to="/">{title}</Link>
        </h1>
      </div>
    </header>
  )
}
