import * as React from 'react'
import classNames from 'classnames'
import Bio from './bio'
import Footer from './footer'
import Header from './header'

const Layout = ({
  location,
  showSidebar = true,
  title,
  mainContainerProps = {},
  children,
}) => {
  // const rootPath = `${__PATH_PREFIX__}/`
  // const isRootPath = location.pathname === rootPath

  const { className, style, ...restMainContainerProps } = mainContainerProps
  const mainContainerClassName = classNames([
    'container mx-auto flex pt-6',
    className,
  ])
  const mainContainerStyle = {
    // 180px = (header + footer) height,
    minHeight: `calc(100vh - 180px)`,
    ...style,
  }

  return (
    <>
      <Header title={title} />
      <div
        className={mainContainerClassName}
        style={mainContainerStyle}
        {...restMainContainerProps}
      >
        {showSidebar && (
          <div className=" flex-shrink-0 mr-10 w-[240px]">
            <Bio />
          </div>
        )}
        <main className="grow">{children}</main>
      </div>
      <Footer />
    </>
  )
}

export default Layout
